'use strict';
import React from 'react';
import cx from 'classnames';
import Button from './VoteButton';

require('styles/VoteArea.scss');

class VoteButtonComponent extends React.Component {

  static defaultProps = {
    buttons: [
      {
        'name': 'Cierto',
        'meaning': 'La afirmación refleja la información más reciente y fidedigna en el asunto.',
        'value': 1
      },
      {
        'name': 'Cierto pero',
        'meaning': 'La afirmación refleja la información más reciente y fidedigna en el asunto pero deja por fuera un aspecto relevante para comprenderlo.',
        'value': 2
      },
      {
        'name': 'Apresurado',
        'meaning': 'La afirmación se refiere a un hecho que no ha culminado y por lo tanto se anticipa a sus conclusiones.',
        'value': 3
      },
      {
        'name': 'Debatible',
        'meaning': 'La afirmación corresponde a una posición, lectura o interpretación de un hecho que tiene fundamentos sólidos, pero existen otras alternativas que también tienen sustentos fuertes.',
        'value': 4
      },
      {
        'name': 'Exagerado',
        'meaning': 'La afirmación refleja una tendencia o dato cierto pero lo lleva más allá de lo que muesrtra la información más reciente y fidedigna en el asunto.',
        'value': 5
      },
      {
        'name': 'Engañoso',
        'meaning': 'La afirmación es un argumento que parte de un hecho real para llegar a una conclusión incorrecta.',
        'value': 6
      },
      {
        'name': 'Falso',
        'meaning': 'La afirmación resulta falsa a la luz de la información más reciente y fidedigna en el asunto.',
        'value': 7
      },
      {
        'name': 'Inchequeable',
        'meaning': 'La afirmación no puede ser revisada ya sea porque no hay información reciente y fidedigna sobre el asunto o porque se trata de una opinión.',
        'value': 8
      }
    ]
  };

  constructor() {
    super();

    this.state = {
      selected: false,
      showConventions: false
    };

    this.setInchequable = this.setInchequable.bind(this);
    this.handleSelection = this.handleSelection.bind(this);
    this.showConventions = this.showConventions.bind(this);
  }

  setInchequable() {
    this.handleSelection(9);
  }

  handleSelection(id) {
    this.setState({ selected: id });
    this.props.callback(id);
  }

  getButtons() {
    // console.log(this.props);
    return this.props.buttons.map((button, index) => {
      let active = false,
        thisIsIt = false;
      if (this.props.selected === button.value && this.props.showResult) {
        active = true;
      }

      if (this.props.showResult) {
        // console.log(this.props.score, button.value);
        if (Number(button.value) === Number(this.props.score)) {
          // console.log('this');
          thisIsIt = true;
        } else {
          thisIsIt = 2;
        }
      }

      return (
        <li
          className={cx(
            'VoteArea__list-item',
            { 'VoteArea__list-item--hidden': thisIsIt === 2 }
          )}
          key={index}
        >
          <Button
            active={active}
            thisIsIt={thisIsIt}
            callback={this.handleSelection}
            {...button}
          />
        </li>
      )
    });
  }

  showConventions() {
    this.setState({ showConventions: !this.state.showConventions });
  }

  getDescriptions() {
    if (this.state.showConventions) {
      return (
        <div>
          <table style={{textAlign: 'left', maxWidth: '45em', float: 'right'}} className="table">
            <tbody>
            <tr>
              <td>Cierto</td>
              <td>La afirmación refleja la información más reciente y fidedigna en el asunto.</td>
            </tr>
            <tr>
              <td>Cierto pero</td>
              <td>La afirmación refleja la información más reciente y fidedigna en el asunto pero deja por fuera un
                aspecto relevante para comprenderlo.
              </td>
            </tr>
            <tr>
              <td>Apresurado</td>
              <td>La afirmación se refiere a un hecho que no ha culminado y por lo tanto se anticipa a sus
                conclusiones.
              </td>
            </tr>
            <tr>
              <td>Debatible</td>
              <td>La afirmación corresponde a una posición, lectura o interpretación de un hecho que tiene fundamentos
                sólidos, pero existen otras alternativas que también tienen sustentos fuertes.
              </td>
            </tr>
            <tr>
              <td>Exagerado</td>
              <td>La afirmación refleja una tendencia o dato cierto pero lo lleva más allá de lo que muesrtra la
                información más reciente y fidedigna en el asunto.
              </td>
            </tr>
            <tr>
              <td>Engañoso</td>
              <td>La afirmación es un argumento que parte de un hecho real para llegar a una conclusión incorrecta.
              </td>
            </tr>
            <tr>
              <td>Falso</td>
              <td>La afirmación resulta falsa a la luz de la información más reciente y fidedigna en el asunto.</td>
            </tr>
            <tr>
              <td>Inchequable</td>
              <td>La afirmación no puede ser revisada ya sea porque no hay información reciente y fidedigna sobre el
                asunto o porque se trata de una opinión.
              </td>
            </tr>
            </tbody>
          </table>
        </div>
      )
    }
  }

  render() {
    const buttons = this.getButtons();
    let inchequableActive = false;
    if (this.props.selected === 8) inchequableActive = true;
    const descriptions = this.getDescriptions();

    return (
      <div className={cx(
        'VoteArea'
      )}>
        <h4 className="VoteArea__title">¿Verdad?</h4>

        <div className={cx(
          'VoteArea__form-container',
          { 'VoteArea--show-result': this.props.showResult },
          { [`VoteArea--score-${Math.round(this.props.questionScore)}`]: this.props.showResult }
        )}>
          <div className={cx(
            'VoteArea__slide',
            { 'VoteArea__slide--small': this.props.showResult }
          )} />
          <ul className={cx(
            'VoteArea__button-list',
            { 'VoteArea__button-list--small': this.props.showResult }
          )}>
            {buttons}
          </ul>
        </div>
        <div className={cx(
          'VoteArea__footer',
          { 'VoteArea__footer--hidden': this.props.showResult },
        )}>
          {/*<small>- O -</small>*/}
          <button onClick={this.setInchequable}
                  className={cx(
                    'VoteArea__button',
                    { 'VoteArea__button--selected': inchequableActive && this.props.showResult },
                  )}>
            No lo sé
          </button>
          {/*<button onClick={this.showConventions} style={{ position: 'absolute', right: 0 }}*/}
                  {/*className="VoteArea__button">View descriptions*/}
          {/*</button>*/}
          {descriptions}
        </div>
      </div>
    );
  }
}

VoteButtonComponent.displayName = 'VoteButton';

// Uncomment properties you need
// VoteButtonComponent.propTypes = {};

export default VoteButtonComponent;



